.TodoList {
  width: 100%;

  &__user-button--selected {
    background-color: #4d457b;
  }

  &__list {
    list-style: none;
    padding-left: 5px;
  }

  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 10px;
    padding: 10px;
    margin-bottom: 5px;
  }

  &__item--checked {
    background-color: #cde4e4;
    color: #459595;
  }

  &__item--unchecked {
    background-color: #ffe0db;
    color: #ff3626;
  }
}

.error-message {
  display: block;
  margin: 15px;
  padding: 5px 10px;
  font-size: 18px;
  width: 40%;
  min-width: 300px;
  font-weight: bold;
  border-radius: 5px;
  color: darkred;
  background-color: pink;
  transition: background-color 0.12s;
}
